import Vue from "vue";
import Router from "vue-router";
import DashboardLayout from './views/Dashboard/DashboardLayout.vue';

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      component: DashboardLayout,
      children: [
        {
          path: "/",
          name: "demo",
          component: () => import("./views/Dashboard/DemoContent/DemoContent.vue")
        },
        {
          path: "/employees",
          name: "employees",
          component: () => import("./views/Dashboard/Employees/Employees.vue")
        },
        {
          path: "/establishments",
          name: "establishments",
          component: () => import("./views/Dashboard/Establishments/Establishments.vue")
        },
        {
          path: "/profile",
          name: "profile",
          component: () => import("./views/Dashboard/Profile/Profile.vue")
        },
      ]
    },
    { path: "/auth", component: () => import("./views/Auth/Layout.vue"),
      children: [
        {
          path: "login",
          name: "login",
          component: () => import("./views/Auth/Login.vue")
        },
        {
          path: "register",
          name: "register",
          component: () => import("./views/Auth/Register.vue")
        },
      ]
    }
  ]
});
