






































import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class MenuLink extends Vue {
  @Prop() private icon!: string; //класс иконки
  @Prop() private name!: string; //название ссылки
  @Prop() private type!: any; //название ссылки
  @Prop() link!: string; //ccылка
  data() {
    return {
      itemType: this.type === undefined ? "link" : this.type, //default for prop "type"
      itemCollapsed: false
    };
  };
}
